<template>
  <div>
    <main> <!-- Début du main -->
        <form class="admin">
          <h1>Editer "{{getArtwork.name}}" de {{getArtwork.person}}</h1>
          <label>Nom:<input v-model="artwork.name"></label><br>
          <label>Date: <input v-model="artwork.date"></label><br>
          <!-- <label>Auteur: <input v-model="artwork.person"></label><br> -->
          <h3>Contenu</h3>
          <editor v-model="artwork.content"
          :options="defaultOptions"
          height="700px"
          previewStyle="vertical"
          mode="wysiwyg"
          ref="content"/>
          <br>
          <h3>Notes</h3>
          <editor v-model="artwork.notes"
          :options="defaultOptions"
          height="300px"
          previewStyle="vertical"
          mode="wysiwyg"
          ref="notes"/>
          <br>
          <h3>Texte</h3>
          <editor v-model="artwork.text"
          :options="defaultOptions"
          height="500px"
          previewStyle="vertical"
          mode="wysiwyg"
          ref="text"/>
          <StateSelector v-model="artwork.state"/>
          <input type="button" value="Annuler" @click="$router.push($route.path.replace('/edit',''))">
          <input type="button" value="Enregistrer" @click="save()">
          <BarLoader v-show="loading" style="margin: 0 auto; border-radius:5px, display: none"></BarLoader>
        </form>
    </main><!-- fin main -->
  </div>
</template>
<script>
// @ is an alias to /src
import StateSelector from '@/components/state/Selector.vue'
import { mapActions, mapGetters } from 'vuex'
import { Editor } from '@toast-ui/vue-editor'


export default {
  name: 'home',
  components: {
    StateSelector,
    Editor
  },
  data () {
    return {
        artwork: {},
        loading: false,
        defaultOptions: {
            language: 'fr_FR',
            useCommandShortcut: true,
            useDefaultHTMLSanitizer: true,
            usageStatistics: true,
            hideModeSwitch: false,
            toolbarItems: [
                'heading',
                'bold',
                'italic',
                'strike',
                'divider',
                'hr',
                'quote',
                'divider',
                'ul',
                'ol',
                'indent',
                'outdent',
                'divider',
                'table',
                'image',
                'link',
                'divider',
                'code',
                'codeblock'
            ]
        }
    }
  },
  // Fetches posts when the component is created.
  created () {
    window.scrollTo(0, 0)
    this.fetchArtwork(this.$route.params.artwork)
    .then(()=>{
        this.artwork= {...this.getArtwork}
    })
  },
  computed: {
        ...mapGetters('jwt',[
        'jwt',
        'jwtSubject',
        'jwtIssuer'
        ]),
        ...mapGetters('artworks',[
        'getArtwork',
        'artworkLoading'
        ])
  },
  methods: {
    ...mapActions('artworks',[
      'fetchArtwork',
      'setArtworks',
      'updateArtwork'
    ]),
    ...mapActions('alert',[
      'newAlert'
    ]),
    getHtml() {
        return this.$refs.Editor.invoke('getMarkdown')
        // return this.$refs.Editor.invoke('getHtml') -> check github @toast-ui.vue-editor issues, if getHtml method works
    },
    save() {
        this.loading = true;
        this.updateArtwork({
            slug: this.getArtwork.slug,
            name: this.artwork.name, 
            state: this.artwork.state, 
            date: this.artwork.date, 
            person: this.artwork.person, 
            content: this.$refs.content.invoke('getMarkdown'),
            text: this.$refs.text.invoke('getMarkdown'),
            notes: this.$refs.notes.invoke('getMarkdown')
            })
            .then(r=>{
                this.newAlert({type:'success', content:'Successfully edited'})
                this.setArtworks(r.data)
                this.$router.push(`/${this.$route.params.person}/${this.getArtwork.slug}`)
            })
            .catch(e=>{
                this.loading = false;
                this.newAlert({type:'error', content:e.response.data})
            })
    }
  }
}
</script>
