<template>
    <div class="selector" v-if="level >=3">
        <div :class="['option', state.value == getSelected?'selected':'']" v-for="state in dic" :key="state.value" @click="select(state.value)">
            <StateDisplay passive :state="state.value"/>
            <span>{{state.name}}</span>
        </div>
    </div>
</template>
<style scoped>
.selector {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 30px;
}

.option {
    background: var(--b7);
    user-select: none;
    display: flex;
    align-self: center;
    width: auto;
    cursor: pointer !important;
    margin: 5px 10px;
    padding: 7px 12px 7px 7px;
    border-radius: 7px;
}
.option .badge {
    margin-right: 3px;
}
.option.selected {
    background: var(--b3);
    color: var(--b8);
}
</style>
<script>
import StateDisplay from '@/components/state/Display.vue'
import { mapGetters } from 'vuex'

export default {
    components: {
        StateDisplay
    },
    model: {
        prop: 'defaultState',
        event: 'change'
    },
    props: {
        defaultState: String,
        default: String
    },
    data() {
        return {
            dic: [
                {value: 'beginned', name: 'Créé il y a peu de temps'},
                {value: 'writing', name: 'En cours de rédaction'},
                {value: 'soon', name: 'Bientôt prêt'},
                {value: 'certif', name: 'Prêt à réviser'}
            ],
            selected: null
        }
    },
    computed: {
        ...mapGetters('jwt',[
            'level'
        ]),
        getSelected: function() {
            return this.selected?this.selected:this.defaultState
        }
    },
    methods: {
        select(value) {
            this.selected = value;
            this.$emit('change', value);
        }
    }
}
</script>